.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
}
.clock {
    font-size: 50px;
    font-style: italic;
    color: #494949;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
}
.clockAnaloge{
    width: 400px;
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../img/clock.png");
    box-shadow: 0 -15px 15px rgba(134, 134, 134, 0.35),
    inset 0 -15px 15px #191919,
    0 10px 10px rgba(134, 134, 134, 0.35),
    inset 0 10px 10px #191919;
    border-radius: 50%;
    background-color: #494949;
}
.clockAnaloge:before{
    content: "";
    position: absolute;
    width: 15px;
    height: 15px;
    background-color: white;
    border-radius: 50%;
    z-index: 10;
}
.clockAnaloge .hour,
.clockAnaloge .min,
.clockAnaloge .sec{
    position: absolute;
}
.clockAnaloge .hour, .hr{
    width: 160px;
    height: 160px;
}
.clockAnaloge .min, .mn{
    width: 190px;
    height: 190px;
}
.clockAnaloge .sec, .sc{
    width: 230px;
    height: 230px;
}
.hr, .mn, .sc{
    display: flex;
    justify-content: center;
    position: absolute;
    border-radius: 50%;
}
.hr:before{
    content: '';
    position: absolute;
    width: 8px;
    height: 80px;
    background-color: #ffc600;
    z-index: 1;
    border-radius: 6px 6px 0 0;
}
.mn:before{
    content: '';
    position: absolute;
    width: 4px;
    height: 90px;
    background-color: #fff;
    z-index: 2;
    border-radius: 6px 6px 0 0;
}
.sc:before{
    content: '';
    position: absolute;
    width: 2px;
    height: 150px;
    background-color: #fff;
    z-index: 3;
    border-radius: 6px 6px 0 0;
}
.btn{
    margin-top: 20px;
    font-size: 22px;
    font-weight: 600;
    cursor: pointer;
    transition: 0.2s all ease-in;
}
.btn:hover{
    background-color: yellow;
    border-radius: 10px;
}